  title: `Divertissements`,
  p1: `La photosensibilité, c’est à-dire la sensibilité à la lumière produit par les différents appareils électroniques (télévision, cellulaires, etc.) est un déclencheur potentiel de crise. On dit qu’une personne est photosensible si elle a une crise d’épilepsie après avoir été exposé à des scintillements et des clignotants des lumières d’une certaine intensité et à un rythme régulier. Les crises peuvent donc être provoquées par certains jeux vidéos et films chez certains utilisateurs. Cependant, la photosensibilité demeure rare et représente moins de 5 % du 1 % des personnes épileptiques. Cette photosensibilité semble aussi diminuer avec l’âge. Renseignez-vous auprès de votre médecin pour connaître vos risques d’hypersensibilité.`
