  p1: `En tant que parent ou futur parent qui reçoit un diagnostic d’épilepsie, une question que vous pourriez vous poser est celle de savoir si vous pouvez transmettre l’épilepsie à votre enfant. Les études scientifiques montrent qu’il y a une transmission génétique de l’épilepsie. Le risque qu'un enfant ait l'épilepsie est légèrement plus élevé dans le cas de parents ayant eux-mêmes l'épilepsie, soit de 6%.`,
